@import "Components/about/Left.scss";
@import "Components/about/Right.scss";

#about{
    display: flex;
    height: fit-content;
    align-items: center;
    justify-content: space-between;
}
@media only screen and (max-width: 768px){
    #about{
        flex-direction: column-reverse;
        justify-content: center;
        gap: 2em;
    }
}